body {
  margin: 0 ;
  font-family: 'Poppins', sans-serif;
}
/* header*/
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  background-color: #ffff;
}

.nav--logo{
  font-weight: 600;
  font-size: 24px;
}
.logo--unwind{
  color:  #166527;
}

.logo--cabins{
  color: #1A3724;
}

.nav--info{
  display: flex;
  gap: 32px;
  font-weight: 400;
}

/*body*/

/*body--hero*/
.hero--wrap{
  display: flex;
  flex-wrap: wrap;
}
.hero--section-left{
  background:#2E2E35;
  height: 500px;
  flex-basis: 40%;
}
.hero--section-right{
  background:url(./travel_website/img/hero-selection.avif) no-repeat  0px  ;
  height: 500px;
  flex-basis: 60%;
  background-size: cover;
}

.hero--section-cover{
  background:linear-gradient(to right, #18181b, rgba(0, 0, 0, 0));
  height: 500px;
  position: absolute;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.hero--section-article{
  color: #ffff;
  max-width: 544px;
  padding-left: 23%;
  padding-top: 96px;
}
.hero--section-header{

  font-size: 44px;
  line-height: 56px;
  font-family: 'Source Serif Pro', serif;
}

.hero--section-header >span:last-of-type{
  color:#F5B963;
}

.hero--section-article > p{
  line-height: 28px;
  font-size: 16px;
}

.hero-section-rewiew{
  display: flex;
  align-items: center;
}

.hero--section-imgContainer>img{
  outline: solid rgb(0, 0, 0) 1px;
  border-radius: 50%; 
  position: relative;
}

.hero--section-imgContainer>img:first-of-type{
  right: 0px;
}
.hero--section-imgContainer>img:nth-of-type(2){
  right: 5px;
}
.hero--section-imgContainer>img:nth-of-type(3){
  right: 10px;
}
.hero--section-imgContainer>img:nth-of-type(4){
  right: 15px;
}
.hero--section-imgContainer>img:nth-of-type(5){
  right: 20px;
}

.trustPilot-container{
  display: flex;
  align-items: center;
  color: #ffff;
}
.trustPilot-container>div:first-child{
padding-right: 14px;

}
.trustPilot-container>div:first-child>svg{
  color: #1BB580;
}
.trustPilot-container>div:last-child>svg{
  background-color: #1BB580;
  margin-right: 1px;
  width: 15px;
  padding: 2px;
}

.trustPilot-container>div:last-child>span{
  padding-left: 12px;
  font-weight: 200;
}

/*Search bar */ 
.searchBar--container{

  position: absolute;
  bottom: -45px;
  display: flex;
  
  align-items: center;
  background-color: #FFFFFF;
  width: 90%;
  left: 15%;
  height: 100px;
  padding:  0px 27px;
  box-shadow: 1px 0px 42px 9px rgba(0,0,0,1);
  -webkit-box-shadow: 1px 0px 42px 9px rgba(0,0,0,1);
  -moz-box-shadow: 1px 0px 42px 9px rgba(0,0,0,1);
  border-radius: 5px;
}
.searchBar--container>input{
  background-color: #EAEAEA;
  margin-right: 8px;
  line-height: 40px;
  font-size: 16px;
  padding-left: 4%;

  border: none;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: 10% center;
  
}
.search-input{
  background-image:url(./travel_website/img/icons/map.png)  ;
  width: 20%;
}
.datepicker{
  background-image:url(./travel_website/img/icons/calendar.png)  ;
  width: 12%;
}

.set-people-input{
  background-image:url(./travel_website/img/icons/people.png)  ;
  width: 12%;
}

.searchBar--container>input:last-of-type{
  background-color: #233D2C;
  color: #ffff;
 
 padding: 0px 40px;
}

.locations-active{
  display: flex;

  flex-direction: column;
  color: #18181b;
  background-color: #EAEAEA;
  width: 20%;
  border:solid 1px black;
  position: absolute;
  left: 2%;
  top: 70px;
  box-shadow: -3px 20px 51px -7px rgba(0,0,0,0.9);
  -webkit-box-shadow: -3px 20px 51px -7px rgba(0,0,0,0.9);
  -moz-box-shadow: -3px 20px 51px -7px rgba(0,0,0,0.9);
  border-radius: 2px;
  
}
.locations-active>li{
padding-left: 0px;
margin-left: 0px;
list-style: none; 
}


ul{
  margin: 0px;
  padding: 0px;
}


.locations-active>li:hover{
  background-color: #DCDCDC;
  cursor: pointer;

}



input[type="date"]::-webkit-calendar-picker-indicator {
  background: transparent;
 
  bottom: 0;
  color: transparent;
  cursor: pointer;
  height: auto;
  left: 26%;
  position: absolute;
  top: 25px;
  width: 15%;
  height: 40px;
  
}

